@import "../../Common/Colors.scss";

.ThemePadding {
  justify-content: space-evenly;
  padding-top: 10px;
}
.ThemeForm {
  border: 1px solid $borderWhite;
  padding: 10px;
  background: $white;
  .ant-collapse > .ant-collapse-item > .ant-collapse-header {
    font-weight: 500;
    background-color: #f5f5f5;
    // color: $white;
    // background-color: #40a9ff;
  }
  .ant-collapse-content {
    overflow: visible;
  }
}

.WidgetTheme {
  position: fixed;
  top: 74px;
  right: 1.2%;
  width: 29.5%;
  border: 1px solid $borderWhite;
  padding: 10px;
  background: $white;
}
.ThemeHeader {
  margin-top: 0px !important;
}
.ThemeLabel .ant-form-item-label {
  display: flex;
  justify-content: center;
  flex-direction: column;
  label {
    font-size: 13px;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.65);
  }
}
.ThemeBorder {
  border: 1px solid $borderWhite;
  .ant-divider::before {
    top: 0;
  }
  .ant-divider::after {
    top: 0;
  }
}
.ThemeButton {
  margin-top: 20px;
  margin-bottom: 10px;
}
